.btn-container{
    display: block;
    width: 100%;
    margin-left: 30px;
}
.langIconHide{
    display: none;
}

.langIcon{
    cursor: pointer;
}
.langIcon:hover{
    background-color: hsl(0, 0%, 60%);
    border-radius: 50px;
}
.langIcon:active{
    background-color: hsl(0, 0%, 70%);
}